.footer {
    background: #202020;
}

.footer__container {
    display: grid;
    grid-template-columns: repeat(12, minmax(0, 80px));
    gap: 30px;
    justify-items: start;
    align-items: center;
    grid-template-areas: "logo logo logo logo text text text text text soc soc soc";
    padding-top: 24px;
    padding-bottom: 16px;
}

.footer__logo {
    grid-area: logo;
}

.footer__logo .logo__img {
    display: flex;
    width: 190px;
    height: 30.7px;
    margin-bottom: 7px;
}

.footer__logo .logo__descr {
    font-size: 14px;
    line-height: 18px;
    color: #E9E9E9;
}

.footer__text {
    grid-area: text;
}

.footer__text .text {
    max-width: 361px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #CACACA;
}

.footer__social {
    grid-area: soc;
    justify-self: end;
}

.footer__social .social__title {
    margin-bottom: 8px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #CACACA;
}

.m-0 {
    margin: 0;
}

.footer__social .social__links {
    display: flex;
    justify-content: center;
}

.ok {
    margin-right: 16px;
}

.footer__social .social__links svg {
    cursor: pointer;
}

.social__link{
    fill: #CACACA;
    transition: fill 0.3s;
    outline: none;
}
.social__link:hover{
    fill:#FF6E30
}
.social__link:focus:not(:active){
fill: #BC572B;
}
.social__link:active{
    fill: #CE4307;
}
