.works{
    padding-top: 68px;
    padding-bottom: 68px;
}

.works__h2{
    margin-bottom: 12px;
}

.works__container{
    display: grid;
    grid-template-columns: repeat(12, minmax(0, 80px));
    gap: 28px 30px;
}

.works__1{
    grid-column: 2 span;
}

.works__2{
    grid-column: 2 span;
}

.works__3{
    grid-column: 2 span;
}

.works__4{
    grid-column: 6 span;
}

.works__7{
    grid-column: 6 span;
}

.works__5{
    grid-column: 3 span;
    order: 6;
}

.works__6{
    grid-column: 3 span;
    order: 7;
}

.works__content{
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: flex-end;

}

.works__list{
    position: relative;
}

.date{
    font-family: 'Roboto';
    font-size: 14px;
    font-weight: 400;
    color: #999999;
    margin-top: auto;
    display: flex;
    align-items: center;
}

.works__content-bg{
    position: absolute;
    color: #FFFFFF;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    max-width: 352px;
    padding: 26px 30px 34px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    flex-wrap: wrap;
}

.more__works{
    position: absolute;
    top: 7px;
    right: 7px;
    background-color: #202020;
    color: #ffffff;
}

.works__desk{
    font-family: 'Roboto';
font-size: 16px;
font-weight: 400;
line-height: 22px;
letter-spacing: 0em;
text-align: left;

}

.works__h3{
    margin: 0;
    margin-bottom: 6px;
font-size: 24px;
font-weight: 700;
line-height: 24px;
letter-spacing: 0em;
text-align: left;

}

.works__p{
    font-family: 'Roboto';
    font-size: 16px;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 0em;
    text-align: left;    
}

.works__btn-ps{
    margin-top: auto;
}

.works__btn{
height: 50px;
padding: 15px 22px 15px 22px;
border: solid 3px #FF6E30;
background: transparent;
font-family: 'Roboto';
font-size: 16px;
font-weight: 700;
line-height: 19px;
letter-spacing: 0em;
text-align: left;
color: #FFFFFF;
transition: background-color 0.3s;
}

.works__btn:focus{
background-color: #BC572B;
border: solid 3px #FF6E30;
}

.works__btn:active{
background-color: #CE4307;
border: solid 3px #CE4307;
}

.works__btn:hover{
background-color: #FF6E30;
border: solid 3px #FF6E30;
}

.works .picture__bg {
    display: flex;
    height: 100%;
}

.project-article__headline:focus {
    outline: 0;
    background-color: #bc572b;
    color: #fff
}

.project-article__headline:active {
    color: #ce4307
}

.project-article__headline:hover {
    color: #FF6E30
}
