.header {	
	color: #ffffff;
}

.header__container--top{
	background: #202020;
	position: relative;
}
  
.header__container {
	display: grid;
	grid-template-columns: repeat(12, minmax(0, 80px));
	grid-template-areas: "logo logo logo nav nav nav nav nav nav nav search search";
	gap: 30px;
	align-items: center;
	padding-top: 24px;
	padding-bottom: 25.3px;
}

.header__logo{
	grid-area: logo;
}

.header__nav--top{
	grid-area: nav;
}

.header__list--top{
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.header__search{
	grid-area: search;
	justify-self: end;
	position: relative;
}

.header__bottom{
	display: grid;
	grid-template-columns: repeat(6, minmax(0, 112px));
	grid-template-areas: "nav nav nav nav nav nav";
	gap: 30px;
	align-items: center;
	padding-top: 12px;
	padding-bottom: 13px;
}

.header__nav--bottom{
	grid-area: nav;
}

.header__list--bottom{
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.header__container--bottom{
	display: none;
	background-color: #4A4A4A;
}

.header__burger--menu{
	background-color: #202020;
	display: none;
	padding-bottom: 36px;
}
.burger__cloze{
	margin-bottom: 16px;
}

.inp__header{
	width: 170px;
height: 18px;
border: none;
color: #ffffff;
border-bottom:solid 1px #E9E9E9 ;
background-color: transparent;
}

.burger--items{
margin-bottom: 30px;
}

.burger__tel{
	margin: 0;
	margin-top: 212px;
}
.btn__burger--open{
	display: none;
}

.header__list-a{
	transition: color 0.3s;
}

.header__list-a:focus:not(:hover){
color: #ffffff;
background-color: #BC572B;
}

.header__list-a:active{
color: #CE4307;
background-color: none;
}

.header__list-a:hover{
	color: #FF6E30;
}

.header__btn{
	position: absolute;
	top: 7px; right: 10px;
	height: 30px;
}

.search{
	position: absolute;
	top: 25px;
    right: 22px;
    width: 278px;
	display: none ;
}

.search__form {
    padding-right: 2px;
    padding-bottom: 4px;
    display: flex;
    background-color: #202020;
	
}
.search-form__input {
    width: 100%;
    margin-right: 9px;
    background-color: #202020;
    border-bottom: 1px solid #fff;
    font-family: TTFirsNeue,sans-serif;
    color: #fff;
    transition: border-color .3s ease;
}