.about{
    background-color: #202020;
    color: #ffffff;
}

.about__container{
    padding-top: 30px;
    padding-bottom: 70px;
}

.about__list{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-auto-flow: row;
    gap: 22px 30px
}

.about__form{
    display: grid;
    grid-template-columns: 4fr 1fr;
    grid-auto-flow: row;
    gap: 20px 30px;
}

.about__input{
    padding: 15px 23px;
    width: 100%;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    box-sizing: border-box;
    border: none;
    resize: none;
    background-color: #4A4A4A;
}

.about__h2{
    margin-bottom: 5px;
font-size: 24px;
font-weight: 700;
line-height: 31px;
letter-spacing: 0em;
text-align: left;

}

.about__p{
    font-family: 'Roboto';
    font-size: 16px;
    font-weight: 400;
    line-height: 28px;
    letter-spacing: 0em;
    text-align: left;    
}

.about__img{
    margin-bottom: 15px;
}

.about__h3{
    margin: 0;
    margin-bottom: 16px;
font-size: 24px;
font-weight: 400;
line-height: 22px;
letter-spacing: 0em;
text-align: left;

}