.yandex-map {
    
    height: 100%;
    
    
}

.contacts__map{
    grid-column: 8 span;
    position: relative;
}

[class*="ground-pane"] {
    -webkit-filter: grayscale(1);
            filter: grayscale(1);
}

.container__contacts{
    display: grid;
    grid-template-columns: repeat(12, minmax(0, 80px));
    grid-auto-rows: 460px;
    gap: 30px;
    position: relative;
}

.cont-more{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: calc((100% - 30px)/2);
    background-color: #202020D9;
}

.cart-cont{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    padding: 70px 60px 50px;
}

.cont-more__h3{
font-size: 24px;
font-weight: 400;
line-height: 22px;
letter-spacing: 0em;
text-align: left;
margin: 0;
margin-bottom: 5px;
color: #ffffff;
}

.cont-more__desk{
    font-family: 'Roboto';
font-size: 16px;
font-weight: 400;
line-height: 28px;
letter-spacing: 0em;
text-align: left;
margin-bottom: 2px;
color: #CACACA;
}

.open--cont{
    position: absolute;
    top: 0;
    display: none;
}

.cont-more__a{
font-size: 16px;
font-weight: 400;
line-height: 21px;
letter-spacing: 0em;
text-align: left;
align-items: center;
color: #ffffff;
}
.contacts__h2{
font-size: 24px;
font-weight: 700;
line-height: 31px;
letter-spacing: 0em;
text-align: left;
margin-bottom: 22px;
}

.form__title {
    margin: 0;
    margin-bottom: 9px;
    font-weight: 400;
    font-size: 24px;
    line-height: 10px;
    color: #202020;
}

 .form__descr {
    margin-bottom: 16px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #999999;
}

.contacts .form__name, .contacts .form__mail {
    position: relative;
}

.contacts .form__input {
    margin-bottom: 20px;
    padding: 15px 23px;
    width: 100%;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    border: none;
    resize: none;
    transition-duration: 0.3s;
    color: #202020;
    background: #E9E9E9;
}

.contacts{
    padding-bottom: 71px;
}

.form__contacts{
    grid-column: 4 span;
}

.cloze--contacts{
    position: absolute;
    top: 20px;
    bottom: 0;
    right: 20px;
}