.project__container{
    padding-top: 41px;
    padding-bottom: 68px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
}

.project__left{
    position: relative;
    color: #ffffff;
}

.project__left-content{
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 60px;
}

.project__right-content{
    display: grid;
    gap: 30px;
    grid-template-columns: repeat(2,1fr);
}

.project__right-content-1{
    grid-column: 2 span;
}

.project__left-span{
    font-family: 'Roboto';
font-size: 16px;
font-weight: 400;
line-height: 19px;
letter-spacing: 0em;
text-align: left;
margin-bottom: 3px;
}
.works__img{
    margin-bottom: 12px;
}
.more{
    position: absolute;
    top: 15px;
    right: 15px;
    background-color: #202020;
}

.more__p{
    padding: 7px 15px;
    font-family: 'Roboto';
font-size: 14px;
font-weight: 400;
line-height: 16px;
letter-spacing: 0em;
text-align: center;

}

.project__left-h2{
    font-size: 36px;
    font-weight: 700;
    line-height: 37px;
    letter-spacing: 0em;
    text-align: left;
    margin-bottom: 19px;
}

.project__right-h2{
font-size: 24px;
font-weight: 700;
line-height: 25px;
letter-spacing: 0em;
text-align: left;
margin-bottom: 18px;
}

.project picture{
    display: flex;
    height: 100%;
}